import { message } from 'antd';
import { Graphic } from 'react-map-esri';
import { sysConfig } from 'yc';
import { getBusinessDataList, getPumpingAjax, PostPopule, getHeatAjaxZS ,getVehicleAjax,getRenyuanAjax} from '../services/index.js';
import wisdomEyeIMG from './imgaes/设备.png';
import pumpingIMG from './imgaes/泵站.png';
import terminalIMG from './imgaes/终端.png';
import flowIMG from './imgaes/流量.png';
import vehicleIMG from './imgaes/车辆.png';
import personnelIMG from './imgaes/人员.png';

const config = sysConfig.getCfgByKey('layersId');

// 配置数据的图标
const imgs = {
  pumping: pumpingIMG,
  wisdomEye: wisdomEyeIMG,
  terminal: terminalIMG,
  flow: flowIMG,
  vehicle: vehicleIMG,
  personnel: personnelIMG,
}
// 重新刷取数据结构
const Arrs = (fe, img, fil) => {
  const item = [];
  if (!imgs[img]) return message.error('没有配置当前图层，请联系研发人员');
  if (fe) {
    fe.filter(y => y.attributes[fil] !== '').map(x => {
      const { attributes } = x;
      const pointParams = {
        src: imgs[img],
        width: 34,
        height: 42,
        x: attributes.横坐标 || attributes.X,
        y: attributes.纵坐标 || attributes.Y,
        value: attributes,
      };
      item.push(pointParams);
    });
  }
  return item;
}
// 重新刷取数据结构(请求过来的数据)
const Arrs2 = (fe, img, fil) => {
  const item = [];
  if (!imgs[img]) return message.error('没有配置当前图层，请联系研发人员');
  if (fe) {
    fe.filter(y => y[fil] !== '').map(x => {
      const pointParams = {
        src: imgs[img],
        width: 34,
        height: 42,
        x: x.x,
        y: x.y,
        value: x,
      };
      item.push(pointParams);
    });
  }
  return item;
}
// 内部循环构建图层
const Maps = (arr) => {
  const item = arr.map((key, index) => {
    const pumpkey = `pump_${index}`;
    return (<Graphic key={pumpkey} attributes={{ val: key.value, gemo: { x: key.x, y: key.y } }} geometry={{ type: 'point', x: key.x, y: key.y, }} symbol={{ type: 'picture-marker', url: key.src, width: key.width, height: key.height }} ></Graphic>)
  })
  return item;
}

// 去除英文字段
const regEngline = (arr) => {
  const item = [];
  const reg = /^[a-zA-Z]+$/;
  arr.map(x => {
    const f = {};
    Object.keys(x.value).map(y => { if (!reg.test(y)) return f[y] = x.value[y] });
    item.push(f);
  });
  return item;
}

export default {
  namespace: 'projectMap',
  state: {
    mapData: [],
    MapGrap: [], // 地图泵站、终端、智慧眼点位
    MapVehicleAndPerson: [], // 车辆  人员
    MapLocalData: {}, // 地图缓存数据
    MapVideo: [], // 地图视频点位
    MapTerminal: [], // 地图终端点位
    HeatMapGrap: [],  // 地图热力图
    MapVideoLayerId: '', // 第三层视频查询的layerId，获取缓存
  },

  effects: {
    *getProjectMap({ payload }, { call, put }) {
      const { projectTypeKey, status, startTime, endTime } = payload;
      let condition = ' 1=1 ';
      if (projectTypeKey) {
        condition += ` and type = '${projectTypeKey}'`;
      }
      if (status) {
        condition += ` and state in (${status})`;
      }
      if (startTime && endTime) {
        condition += ` and createtime >= '${startTime}' and createtime <= '${endTime}'`;
      }
      // 拼接条件参数
      let params = {
        formDefineId: 2334047373625,
        pageNo: 1,
        pageSize: 10000,
        condition: condition,
      }
      const response = yield call(getBusinessDataList, params);

      if (response.msgCode === -1) {
        message.error('网络故障，地图数据获取失败~')
        return;
      }
      const mapData = response.data;
      yield put({
        type: 'loadMapData',
        payload: { mapData },
      })

    },
    // 配置化查询图层信息
    *QueryMapGrap({ payload, layerId }, { call, put, select }) {
      const res = yield call(getPumpingAjax, payload, layerId);
      const projectMap = yield select(state => state.projectMap);
      const { MapLocalData } = projectMap;
      yield put({
        type: 'newState',
        payload: { name: 'layerId', value: layerId }
      })
      const { features, fieldAliases } = res;
      if (features.length < 1) {
        return message.error('没有查询到正确的信息');
      }
      if (!config[layerId]) {
        return message.error('没有查询到对应的图层ID，请检查后再执行!');
      }
      const arr = Arrs(features, config[layerId], 'ALTID');

      // ***************默认显示管网数据**************** */
      if (!MapLocalData[config[layerId]]) {
        const items = regEngline(arr);
        MapLocalData[config[layerId]] = items;
        yield put({
          type: 'newState',
          payload: { name: 'MapLocalData', value: MapLocalData }
        })
      }
      // ***************默认显示管网数据**************** */

      const MapHtml = Maps(arr);
      yield put({
        type: 'newState',
        payload: { name: 'MapGrap', value: MapHtml }
      })
    },

    *QueryMapVehicle({ payload, layerId }, { call, put, select }) {
      const res = yield call(getVehicleAjax, payload, layerId);
      const projectMap = yield select(state => state.projectMap);
      const { MapLocalData } = projectMap;
      yield put({
        type: 'newState',
        payload: { name: 'layerId', value: layerId }
      })
      const { result, fieldAliases } = res;
      if (result.length < 1) {
        return message.error('没有查询到正确的信息');
      }
      if (!config[layerId]) {
        return message.error('没有查询到对应的图层ID，请检查后再执行!');
      }
      const arr = Arrs2(result, config[layerId], 'tid');

      // ***************默认显示管网数据**************** */
     
      if (!MapLocalData[config[layerId]]) {
        MapLocalData[config[layerId]] = arr;
        yield put({
          type: 'newState',
          payload: { name: 'MapLocalData', value: MapLocalData }
        })
      }
      // ***************默认显示管网数据**************** */

      const MapHtml = Maps(arr);
      yield put({
        type: 'newState',
        payload: { name: 'MapVehicleAndPerson', value: MapHtml }
      })
    },

    *QueryMapRenyuan({ payload, layerId }, { call, put, select }) {
      const res = yield call(getRenyuanAjax, payload, layerId);
      const projectMap = yield select(state => state.projectMap);
      const { MapLocalData } = projectMap;
      yield put({
        type: 'newState',
        payload: { name: 'layerId', value: layerId }
      })
      const { result, fieldAliases } = res;
      if (result.length < 1) {
        return message.error('没有查询到正确的信息');
      }
      if (!config[layerId]) {
        return message.error('没有查询到对应的图层ID，请检查后再执行!');
      }
      const arr = Arrs2(result, config[layerId], 'jd');

      // ***************默认显示管网数据**************** */
      if (!MapLocalData[config[layerId]]) {
        //const items = regEngline(arr);
        MapLocalData[config[layerId]] = arr;
        yield put({
          type: 'newState',
          payload: { name: 'MapLocalData', value: MapLocalData }
        })
      }
      // ***************默认显示管网数据**************** */

      const MapHtml = Maps(arr);
      yield put({
        type: 'newState',
        payload: { name: 'MapVehicleAndPerson', value: MapHtml }
      })
    },

    *QueryTerminal({ payload, layerId }, { call, put, select }) {
      const projectMap = yield select(state => state.projectMap);
      const { MapLocalData } = projectMap;
      const res = yield call(getPumpingAjax, payload, layerId);
      yield put({
        type: 'newState',
        payload: { name: 'layerId', value: layerId }
      })
      const { features } = res;
      if (features.length < 1) {
        return message.error('没有查询到正确的信息');
      }
      if (!config[layerId]) {
        return message.error('没有查询到对应的图层ID，请检查后再执行!');
      }
      const arr = Arrs(features, config[layerId], 'ALTID');
      // ***************默认显示管网数据**************** */
      if (!MapLocalData[config[layerId]]) {
        const items = regEngline(arr);
        MapLocalData[config[layerId]] = items;
        yield put({
          type: 'newState',
          payload: { name: 'MapLocalData', value: MapLocalData }
        })
      }
      // ***************默认显示管网数据**************** */
      const MapHtml = Maps(arr);
      yield put({
        type: 'newState',
        payload: { name: 'MapTerminal', value: MapHtml }
      });
    },
    // 第三层层视频接口
    *QueryMapVideo({ payload, layerId }, { call, put, select }) {
      const projectMap = yield select(state => state.projectMap);
      const { MapLocalData } = projectMap;
      const res = yield call(getPumpingAjax, payload, layerId);
      yield put({
        type: 'newState',
        payload: { name: 'layerId', value: layerId }
      })
      const { features } = res;
      if (features.length < 1) {
        return message.error('没有查询到正确的信息');
      }
      if (!config[layerId]) {
        return message.error('没有查询到对应的图层ID，请检查后再执行!');
      }
      const arr = Arrs(features, config[layerId], 'ALTID');

      // ***************默认显示管网数据**************** */
      if (!MapLocalData[config[layerId]]) {
        const items = regEngline(arr);
        MapLocalData[config[layerId]] = items;
        yield put({
          type: 'newState',
          payload: { name: 'MapLocalData', value: MapLocalData }
        })
      }
      // ***************默认显示管网数据**************** */
      const MapHtml = Maps(arr);
      yield put({
        type: 'newState',
        payload: { name: 'MapVideo', value: MapHtml }
      })
      yield put({
        type: 'newState',
        payload: { name: 'MapVideoLayerId', value: config[layerId] }
      });
    },

    // 热力流量图
    *QueryHeat({ payload, layerId }, { call, put }) {
      const res = yield call(getHeatAjaxZS, payload, layerId);
      const { result1,result2 } = res;
      if (result2.length < 1) {
        return message.error('没有查询到正确的信息');
      }
      const arr = Arrs2(result2, config[layerId], 'data').map(x => { return { geometry: { x: x.x, y: x.y } } });
      yield put({
        type: 'newState',
        payload: { name: 'HeatMapGrap', value: arr }
      })
    },

    // 暂时指定查询tip框内信息
    *QeuryPopule({ payload, callback }, { call, put, select }) {
      debugger;
      const projectMap = yield select(state => state.projectMap);
      const { MapLocalData, layerId } = projectMap;
      const name = config[layerId];
      // let ors = '';
      // if (!MapLocalData[name]) {
      // const res = yield call(PostPopule, payload);
      // MapLocalData[name] = res.result;
      // yield put({
      //   type: 'newState',
      //   payload: { name: 'MapLocalData', value: MapLocalData }
      // })
      // }
      const ors = MapLocalData[name].filter(x => x.本点号 === payload);
      callback && callback(...ors, name);
    },
    *QeuryVehicleAndPerson({ payload, callback }, { call, put, select }) {
      debugger;
      const projectMap = yield select(state => state.projectMap);
      const { MapLocalData, layerId } = projectMap;
      const name = config[layerId];
      // let ors = '';
      // if (!MapLocalData[name]) {
      // const res = yield call(PostPopule, payload);
      // MapLocalData[name] = res.result;
      // yield put({
      //   type: 'newState',
      //   payload: { name: 'MapLocalData', value: MapLocalData }
      // })
      // }
      const ors = MapLocalData[name].filter(x => x.value.tid === payload);
      callback && callback(...ors, name);
    },
  },
  reducers: {
    newState(state, { payload }) {
      const { name, value } = payload;
      return {
        ...state,
        [name]: value
      };
    },
    loadMapData(state, { payload }) {
      return { ...state, mapData: payload.mapData };
    },
  },
};
