import React from 'react';
import Normal from './images/状态-运行.png';
import Fault from './images/状态-预警.png';
import Stops from './images/状态-停止.png';
import styles from './style/index.less';

const dw = {
  液位: 'm',
  满溢比: '%',
  瞬时流量: 'm³/h',
  压力: 'Mpa',
  编号: '',
  状态: '',
  占比: '',
  0: Normal,
  1: Fault,
  2: Stops,
}

const items = (key, value) => {
  let span = '';
  if (dw[key]) {
    span = <div className={styles.po_plan_realTime_data_xs_vals}>{value[key]}<span>{dw[key]}</span></div>
  } else if (key === '泵机状态') {
    span = <div className={styles.po_plan_stat_vals}>
      {
      value[key].map((x, y) => <div key={JSON.stringify(x)} className={styles.bjzt}>
      {/* <span style={{ borderImage: `url('./images/${dw[x]}.png') 0 0 0 0 fill round` }}></span> */}
      <img src={dw[x]} style={{ width: 36, height: 46 }} />
      <i>{y + 1}#</i>
    </div>)
      }
    </div>
  } else {
    span = <div className={styles.po_plan_basic_vals}>{value[key]}</div>
  }
  return span;
}

export default class Popule extends React.PureComponent {

  render() {
    const { switchData, onCalick } = this.props;
    return (
      <div className={styles.po} style={{ width: `${switchData.wit || 100}%` }} >
        <div>
          <div className={styles.cal} onClick={() => onCalick()}></div>
          <div className={styles.po_name}>
            <div>{switchData.name}</div>
          </div>
          <div className={styles.po_plan}>
            {
              switchData.bzimgs && Object.keys(switchData.bzimgs).map((item, index) => {
                return (
                  <div key={`${item}${index}`} className={styles.po_plan_basic}>
                    <div className={styles.po_plan_basic_keys}>{item}：</div>
                    {
                      items(item, switchData.bzimgs)
                    }
                  </div>
                )
              })
            }

              {
                switchData.bzbjzt && Object.keys(switchData.bzbjzt).map((item, index) => {
                  return (
                    <div key={`${item}${index}`} className={styles.po_plan_stat}>
                      <div className={styles.po_plan_stat_keys}>{item}：</div>
                      {
                        items(item, switchData.bzbjzt)
                      }
                    </div>
                  )
                })
              }

              <div className={styles.po_plan_realTime}>
                <div className={styles.po_plan_realTime_data}>
                  {
                    switchData.sj && Object.keys(switchData.sj).map((item, index) => {
                      return (
                        <div key={`${item}${index}`} className={styles.po_plan_realTime_data_xs}>
                          <div className={styles.po_plan_realTime_data_xs_keys}>{item}：</div>
                          {
                            items(item, switchData.sj)
                          }
                        </div>
                      )
                    })
                  }
                </div>
                {
                  switchData.img && <div className={styles.po_plan_realTime_imgs}>
                    <img src={`http://192.168.30.212:8088/upload/${switchData.img}.jpg`} />
                  </div>
                }
            </div>
          </div>
        </div>
      </div>
    )
  }

}
