var id = 0;
export function getUUID() {
    return ++id + '';
}
/**
 * 设置实例树，如果实例已经存在，会生成新的实例树
 * @param mapInstances - 实例树
 * @param tag - 标签
 * @param instance - 标签对应的实例
 */
export function setMapInstances(mapInstances, tag, instance) {
    var nextMapInstances = mapInstances;
    if (nextMapInstances === null) {
        nextMapInstances = new Map();
    }
    if (nextMapInstances.has(tag)) {
        // 如果已经存在相同实例类型对象，就需要生成一份新实例对象树
        // 此设定也决定了，不允许出现同类型组件嵌套
        var newInstanceTree = new Map();
        nextMapInstances.forEach(function (value, key) {
            if (key !== tag) {
                newInstanceTree.set(key, value);
            }
        });
        newInstanceTree.set(tag, instance);
        return newInstanceTree;
    } else {
        nextMapInstances.set(tag, instance);
        return nextMapInstances;
    }
}
export function getImageSize(url) {
    return new Promise(function (resolve) {
        var img = new Image();
        // 改变图片的src
        img.src = url;
        // 加载完成执行
        img.onload = function () {
            // 打印
            resolve({
                width: img.width,
                height: img.height
            });
        };
    });
}
