export default class Popule extends React.PureComponent {

  state = {
    view: {}
  }

  componentDidMount() {
    const { MapVideoLayerId, switchData } = this.props;
    const json = locaJson[MapVideoLayerId];
    // 默认取第一个，如果有，就覆盖，没有就继续
    this.setState({ view: json[0] })
    console.log(33333);
    console.log(switchData);
    json.map(x => {
      if (x.name === switchData.泵站名称 || x.name === switchData.终端名称) {
        this.setState({ view: x });
      }
    });
  }

  render() {
    const { onCalick, onOk } = this.props;
    const { view } = this.state;
    return (
      <div className={styles.vi} >
        <div className={styles.vi_gb} onClick={() => onCalick()}>X</div>
        <div className={styles.vi_plan}>
          {
            view.number && view.number.map(x => {
              return <div className={styles.vi_plan_x} onClick={(x => onOk(x))}>{x}#</div>
            })
          }
        </div>
      </div>
    )
  }

}
