import React from 'react';
import { connect } from 'pipenet-core/lib/react';
import { Spin } from 'antd'
import { View, Map, Layer, Tip } from 'react-map-esri';
import Mock from 'mockjs';
import { sysConfig } from 'yc';
import Popule from './mapPopu.js';
import MapsVideo from './mapVideo.js';
import styles from './style/index.less';

const { MapView } = View;
const { TileLayer, GraphicsLayer, VectorLayer, HeatmapLayer } = Layer;

const mockDa = (v) => {
  return Mock.mock({
    zt: `@range(0,2,${v || 0})`,
    'ssll|1-100': 1000,
    'yl|1-0.0001': 1,
    'yw|1-0.0001': 1,
    'zhyyw|1-10.1-10': 1,
    'zhyzb|1-100': 100,
  });
}


const maps = {
  OneMap: 'projectMap/QueryMapGrap',
  TwoMap: 'projectMap/QueryTerminal',
  thridMap: 'projectMap/QueryMapVideo',
}

const newData = (arr, name) => {
  console.log(name);
  
  const item = {};
  console.log(arr);
  switch (name) {
    case 'pumping':
      item.name = arr.泵站名称;
      item.bzimgs = {};
      item.bzimgs['所属部门'] = arr.泵站名称;
      item.bzimgs['站长'] = arr.负责人;
      item.bzbjzt = {};
      item.bzbjzt['泵机状态'] = mockDa(arr.水泵台数).zt;
      item.sj = {};
      item.sj['瞬时流量'] = mockDa().ssll;
      item.sj['压力'] = mockDa().yl;
      item.sj['液位'] = mockDa().yw;
      item['img'] = arr.泵站名称;
      break;
    case 'wisdomEye':
      item.name = arr.站点名称;
      item.sj = {};
      item.sj['编号'] = arr.本点号;
      item.sj['状态'] = '正常';
      item.sj['液位'] = Math.floor(mockDa().zhyyw);
      item.sj['占比'] = `${mockDa().zhyzb}%`;
      item['img'] = arr.站点名称;
      break;
    case 'terminal':
      item.wit = '150';  
      item.name = arr.终端名称;
      item.sj = {};
      item.sj['设计规模'] = arr.设计规模;
      item.sj['受益农户'] = arr.受益农户;
      item.sj['处理工艺'] =  arr.处理工艺;
      break;
    case 'flow':
      item.wit = '150'
      break;
    case 'vehicle':
      item.wit = '100'
      item.name = arr.value.tid;
      item.sj = {};
      item.sj['驾驶员姓名'] = arr.value.jsy;
      item.sj['联系电话'] = arr.value.lxdh;
      item.sj['所属部门'] = arr.value.sybm;
      item.sj['车辆类型'] = arr.value.bz;
      item.sj['日常停车点'] = arr.value.ddtfwz;
      item.sj['日常停车点'] = arr.value.ddtfwz;
      item['img'] = arr.src;
      break;
    case 'personnel':
      item.wit = '100'
      item.name = "人员";
      item.sj = {};
      item.sj['时间'] = arr.value.createtime;
      item.sj['经度'] = arr.value.jd;
      item.sj['纬度'] = arr.value.wd;
      break;
  };
  return item;
}

@connect(state => ({
  projectMap: state.projectMap,
}))

export default class ProjectMap extends React.Component {
  constructor(props) {
    super(props);
    this.projectMapConfig = sysConfig.getCfgByKey('projectMapConfig');
    this.state = {
      switchTip: false,
      switchData: {},
      tipGrop: {},
      loading: false,
      center: {},
      switchVideo: false
    }
  }

  componentWillMount() {
    const { toolParams: { key } } = this.props;
    this.setState({ center: { x: this.projectMapConfig[key].centerx, y: this.projectMapConfig[key].centery } })
  }

  componentDidMount() {

    const { showZD, key } = this.props.toolParams;
    if (showZD) {
      this.props.dispatch({
        type: maps[key],
        layerId: showZD,
        payload: { where: '1=1', geometryType: 'esriGeometryPoint', spatialRel: 'esriSpatialRelIntersects', f: 'json' },
      })
    }
  }
  // 第一页面的图标点击事件
  OnclickMapGrap = (par) => {
    // 使用点位数据获取
    this.setState({ switchTip: false, loading: true })
    this.props.dispatch({
      type: 'projectMap/QeuryPopule',
      payload: par.attributes.val.本点号,
      callback: (res, name) => {
        const da = newData(res, name);
        this.setState({ switchTip: true, switchData: da, tipGrop: par.attributes.val, loading: false, center: { x: par.attributes.gemo.x, y: par.attributes.gemo.y } })
      }
    })
  }
  // 第一页面的图标点击事件(车辆和人员)
  OnclickMapVehicleAndPerson= (par) => {
    debugger;
    // 使用点位数据获取
    this.setState({ switchTip: false, loading: true })
    this.props.dispatch({
      type: 'projectMap/QeuryVehicleAndPerson',
      payload: par.attributes.val.tid,
      callback: (res, name) => {
        const da = newData(res, name);
        this.setState({ switchTip: true, switchData: da, tipGrop: par.attributes.val, loading: false, center: { x: par.attributes.gemo.x, y: par.attributes.gemo.y } })
      }
    })
  }

  // 第二屏页面图标点击事件
  OnclickMapMapTerminal = (par) => {
    this.props.dispatch({
      type: 'zhongduanspace/getZhongduanxx',
      payload: par,
    })
  }

  // 第三页面的图标点击事件
  OnclickMapVideo = (par) => {
    // 使用点位数据获取
    this.setState({ switchVideo: false, loading: true })
    this.props.dispatch({
      type: 'projectMap/QeuryPopule',
      payload: par.attributes.val.本点号,
      callback: res => {
        this.setState({ switchVideo: true, switchData: res, tipGrop: par.attributes.val, loading: false, center: { x: par.attributes.gemo.x, y: par.attributes.gemo.y } })
      }
    })
  }

  // 第三页面视频互动的交互事件
  handleVideoLinkage = (id) => {
    this.props.dispatch({
      type: 'rightmonitor/getRightmonitor',
      payload: id
    })
  }

  render() {
    const { projectMap: { MapGrap, MapTerminal, MapVideo, HeatMapGrap,MapVehicleAndPerson,MapVideoLayerId }, toolParams: { key } } = this.props;
    const { switchTip, switchData, tipGrop, loading, center, switchVideo } = this.state;
    const mapcfg = this.projectMapConfig;
    return (
      <div style={{ width: '100%', height: '100%' }}>
        <div className={styles.mapWrapper}>
          <Spin
            size="large"
            style={{ width: '100%', height: '100%' }}
            className={styles.mapSpin}
            spinning={loading}
            delay={500}
            tip={<span>正在加载中...</span>}
          >
            <MapView
              api={mapcfg[key].api}
              key="projectMap"
              style={{ width: `${mapcfg[key].width}px`, height: `${mapcfg[key].height}px` }}
              center={center}
              defaultUI={false}
              zoom={mapcfg[key].initlevel}
              extent={mapcfg[key].extent}
              constraints={{ minScale: mapcfg[key].minscale, maxScale: mapcfg[key].maxscale }}
            >
              <Map>

                {mapcfg[key].tileLayers.map(item => (
                  <TileLayer url={item.url} key={`tile_${item.id}`} />
                ))}
                {mapcfg[key].vectorLayers.map(item => (
                  <VectorLayer url={item.url} key={`vector_${item.id}`} defaultZoom={mapcfg[key].initlevel} />
                ))}
                <GraphicsLayer id="graphic-pumping" onClick={this.OnclickMapGrap.bind(this)}>
                  {MapGrap}
                </GraphicsLayer>
                <GraphicsLayer id="graphic-vehicleAndPerson" onClick={this.OnclickMapVehicleAndPerson.bind(this)}>
                  {MapVehicleAndPerson}
                </GraphicsLayer>
                <GraphicsLayer id="graphic-terminal" onClick={this.OnclickMapMapTerminal.bind(this)}>
                  {MapTerminal}
                </GraphicsLayer>
                <GraphicsLayer id="graphic-video" onClick={this.OnclickMapVideo.bind(this)}>
                  {MapVideo}
                </GraphicsLayer>
                {
                  switchTip && <Tip id={tipGrop.本点号} key={tipGrop.本点号} point={{ x: center.x, y: center.y }} >
                    <Popule switchData={switchData} onCalick={() => this.setState({ switchTip: false })} />
                  </Tip>
                }
                {
                  switchVideo && <Tip id={tipGrop.本点号} key={tipGrop.本点号} point={{ x: center.x, y: center.y }} >
                    <MapsVideo MapVideoLayerId={MapVideoLayerId} switchData={switchData} onOk={(x) => this.handleVideoLinkage(x)} onCalick={() => this.setState({ switchVideo: false })} />
                  </Tip>
                }
                <HeatmapLayer data={HeatMapGrap} />
              </Map>
            </MapView>
          </Spin>
        </div>
      </div>
    );
  }
}
