import http from 'yc/lib/http';

const { get, post } = http;
/**
 * 获取表单数据列表
 * @param params
 * @returns {Promise<void>}
 */
export async function getBusinessDataList(params) {
  const res = await get({
    svn: 'FORM_SVR',
    path: 'getBusinessFormDataList',
    data: params,
    validate: false,
  })
  return res;
}


/**
 * 获取泵站数据
 * @param params
 * @returns {Promise<void>}
 */
export async function getPumpingAjax(params, layerId) {
  const res = await get({
    svn: 'NET_SVR',
    path: `${layerId}/query`,
    data: params,
    validate: false,
  })
  return res;
}
/**
 * 获取车辆数据
 * @param params
 * @returns {Promise<void>}
 */
export async function getVehicleAjax(params, layerId) {
  const res = await get({
    svn: 'BIG_SCREEN',
    path: 'clAndRy?type=0',
    validate: false,
  })
  return res;
}
